/*** Contents ***/
/* Variables General Header Navigation Graphics Projects About Footer
*/
/*** Variables ***/
/*** Font for the whole page ***/
/*** Yellow colour for graphics ***/
/*** Black used in many places ***/
/*** Used in back to top button ***/
/*** Used for basic borders and back to top button ***/
/*** Used to style links ***/
/*** Used for links on hover ***/
/*** Top and bottom border for the navbar ***/
/*** General ***/
/*** Bump size of code block text ***/
pre { font-size: 2rem; }

/*** Links ***/
a { color: #666; text-decoration: none; cursor: pointer; }
a:hover { color: #c0f; }

/*** Adds space above an element when necessary ***/
.--has-spacing--top { margin-top: 2rem; }

/*** Removes space below an element when necessary ***/
.--no-spacing--bottom { margin-bottom: 0rem; }

/*** Simple border to use whenever ***/
.--simple-border { border: 1px solid #aaa; }

/*** Remove list styling when needed ***/
.--no-list-style { list-style: none; }

/*** All text should get these fonts ***/
.--text { font-family: "Lora", serif; }

/*** Remove this element on narrow screens (see media query below) ***/
.--disappear-when-narrow { display: none; }

/* Larger than phablet */
@media (min-width: 550px) { /*** Screen is wide enough to display these elements ***/
  .--disappear-when-narrow { display: block; } }
/* Larger than desktop */
@media (min-width: 1000px) { /*** Apply to some text so that it fills space and looks better on big screens ***/
  .--bump-text-size { font-size: 2rem; } }
/*** Header ***/
/*** This modification to .container (from Skeleton framework) gets the big
yellow graphic from overflowing the screen. It prevents horizontal scroll. ***/
.container { z-index: 1; position: relative; overflow: hidden; height: 100%; }

/*** Styling for both first and last name ***/
.title { font-size: 6rem; line-height: 1; padding: 0; margin: 0; }

/*** Line between the first and last name ***/
.title__line { margin: 0; padding: 0; height: 1px; display: block; border: 0; border-top: 1px solid #ccc; }

/*** The title underneath my name ***/
.subtitle { padding-left: 4rem; }

/*** Indent the second line on the title slightly ***/
.subtitle__bottom { padding-left: 4rem; }

/*** Media Queries ***/
/* Larger than mobile */
@media (min-width: 400px) { /*** Title can be bigger on screens that aren't tiny ***/
  .title { font-size: 7rem; } }
/* Larger than phablet */
/* Larger than tablet */
@media (min-width: 750px) { /*** On larger screen we can indent the last name ***/
  .title__lastname { padding-left: 4rem; } }
/* Larger than desktop */
/* Larger than Desktop HD */
/*** Navigation ***/
/*** Navigation bar has a top and bottom border and places the text in the
middle vertically using padding ***/
.navbar { border-top: 1px solid #888; border-bottom: 1px solid #888; padding: 1rem 0; height: 2.8rem; }

/*** Navbar items should appear side by side and not in a vertical list ***/
.navbar__item { display: inline; }

/*** The dropdown that is opened when clicking on the appropriate navbar item.
Hidden by default. ***/
.navbar__projects__dropdown { display: none; position: absolute; margin: 1.4rem 0 0 0; min-width: 13em; border: 1px solid #aaa; }

/*** Styling for text inside the dropdown for for projects ***/
.navbar__projects__dropdown__title { font-size: 1.6rem; margin: 0.5rem 0 1rem 1rem; }

/*** The button that allows you to scroll all the way up that appears only when
we scroll past the navbar ***/
.back-to-top { display: none; right: 6%; bottom: 2%; padding: 1rem 2.5rem; border: 1px solid #aaa; border-radius: 35%; font-size: 3rem; position: fixed; z-index: 2; cursor: pointer; background: #ddd; }
.back-to-top:hover { background: #aaa; }

/*** There is space between the navbar items, but no space before the first one ***/
.navbar__item + .navbar__item { margin-left: 2rem; }

/*** Add this class to reveal the dropdown ***/
.navbar__projects__dropdown.--show { display: block; z-index: 2; }

/*** Apply when showing the back to top button ***/
.back-to-top.--show { display: block; z-index: 2; }

/*** Media Queries ***/
/* Larger than mobile */
/* Larger than phablet */
/* Larger than tablet */
@media (min-width: 750px) { /*** Can position the button inside the main content area instead of the margin on larger screens. ***/
  .back-to-top { right: 15%; } }
/* Larger than desktop */
/* Larger than Desktop HD */
/*** Graphics ***/
/*** Graphics used to separate content shouldn't steal clicks from other elements ***/
.graphic { position: relative; z-index: 0; }

/*** Spacing for the big graphic at the top ***/
.graphic--has-spacing { padding-top: 8vw; padding-bottom: 18vw; }

/*** Spacing for smaller straight-line graphics ***/
.graphic--has-spacing--medium { padding-top: 3rem; padding-bottom: 2rem; }

/*** Styles for large graphic seen during page load ***/
.graphic__thick-line { height: 25rem; border: 1px solid #000; position: relative; }

.graphic--yellow { background-color: #ee0; }

.graphic--rotated15 { transform: rotate(-15deg); left: -40%; width: 200%; }

/*** Lines separating content mixin ***/
/*** Black (standard) and yellow versions ***/
.graphic__straight-lines, .graphic__straight-lines--yellow { height: 3rem; position: relative; }

.graphic__straight-lines { border-top: 1rem solid #000; border-bottom: 1.5rem solid #000; }

.graphic__straight-lines--yellow { border-top: 1rem solid #ee0; border-bottom: 1.5rem solid #ee0; }

/*** Media Queries ***/
/* Larger than mobile */
/* Larger than phablet */
@media (min-width: 550px) { /*** Otherwise there is too much room between the graphic and the next heading. ***/
  .graphic--has-spacing { padding-bottom: 14vw; } }
/* Larger than tablet */
/* Larger than desktop */
/* Larger than Desktop HD */
/*** Projects ***/
.project__image { transition: box-shadow .2s, transform .2s ease-in-out; }

.project__list { padding: 2rem 0; }

/*** Media Queries ***/
/* Larger than mobile */
/* Larger than phablet */
/* Larger than tablet */
@media (min-width: 750px) { .project__image:hover { box-shadow: 1px 0.25vw 5px #888888; transform: translate3d(0, -0.5vw, 0); } }
/* Larger than desktop */
@media (min-width: 1000px) { .project__list { font-size: 2rem; } }
/* Larger than Desktop HD */
/*** Footer ***/
/*** Provide spacing between Title and footer content ***/
.footer { margin-top: 2rem; }

/*** Center motto at the bottom of the page ***/
.footer__row--bottom { text-align: center; width: 80%; margin-left: auto; margin-right: auto; }

/*** Each contact information piece title ***/
.footer__row__list__label { padding-left: 1rem; font-weight: bold; }

/*** Makes it easier to click on the links on mobile ***/
.footer__row__list__link { font-size: 1.2em; padding: 1em 0; }

